import React, { useState, useRef, useEffect } from "react";
import { TEC_GOOGLE_PAY_BTN_ID, TEC_GOOGLE_PAY_WRAPPER } from '../consts';
import { validatePrInterface } from '../utils';


const TecGooglePayButton = ({ prInterface, tecPrOptions, teInstanceProps }) => {
    const [ buttonMounted, setButtonMounted ] = useState(false);
    const { mountGoogleButton } = validatePrInterface(prInterface);
    const googlePayTag = useRef(null);
    const { 
        eventRef,  
        requestClient: { instanceId }
    } = teInstanceProps;

    useEffect(() => {
        if (googlePayTag.current && buttonMounted === false) {
            const instanceDetails = {
                instanceId,
                eventRef: eventRef.current
            };

            if (tecPrOptions && tecPrOptions.googlePayOptions)
                instanceDetails.googlePayOptions = tecPrOptions.googlePayOptions;

            mountGoogleButton(TEC_GOOGLE_PAY_BTN_ID, instanceDetails);
            setButtonMounted(true);
        }
    }, [googlePayTag, buttonMounted, tecPrOptions]);
    
    return (
        <div id={ TEC_GOOGLE_PAY_WRAPPER }>
            <div id={ TEC_GOOGLE_PAY_BTN_ID } ref={ googlePayTag }></div>
        </div>
    );
}

export default TecGooglePayButton;
